/*
 * Import custom Sass theme
 */
/*
 * Brand Colors
 */
/*
 * Export global CSS variables
 */
:root {
  --brand-dark-blue: #1a3875;
  --brand-navy-blue: #1a3875;
  --brand-dark-steel: #5b7785;
  --brand-steel-blue: #8aa3b0;
  --brand-light-blue: #c4d1d7;
  --brand-pale-blue: #d6dfe4;
  --brand-plum: #492436;
  --brand-light-plum: #765C69;
  --brand-pale-plum: #ecc8c8;
  --brand-pale-gold: #fef6e4;
  --brand-dark-red: #b62b26;
  --brand-highlight-red: #f5290a;
  --brand-digital-red: #e91f00;
  --brand-black: #000000;
  --brand-white: #ffffff;
  --brand-carbon: #303030;
  --brand-steel: #555555;
  --brand-silver: #b1b1b1;
  --brand-veil: #9c9c9c;
  --brand-cloud: #e3e3e3;
  --brand-fog: #f3f6f7;
  --primary: #1a3875;
  --secondary: #e3e3e3;
  --success: #00892f;
  --info: #555555;
  --warning: #ffa800;
  --danger: #e91f00;
  --light: #e3e3e3;
  --dark: #555555;
  --font-family-sans-serif: "Open Sans", sans-serif;
}
/*
 * Custom Theme
 */
/*
 * Sets Bootstrap Variables with site branding
 */
.accordion .panel-title {
  font-size: 1rem;
}
.accordion [data-toggle=collapse] {
  color: inherit;
  display: block;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  text-decoration: none;
}
.accordion [data-toggle=collapse]:before {
  content: "\e114";
  /* Collapse icon */
  float: left;
  font-family: "Glyphicons Halflings";
  font-size: 11px;
  padding-right: 1rem;
}
.accordion [data-toggle=collapse].collapsed:before {
  content: "\e080";
  /* Expand icon */
  font-family: "Glyphicons Halflings";
}
.breadcrumb {
  background-color: inherit;
  font-size: 1.125rem;
  padding-left: 0;
}
.breadcrumb-item {
  color: #5b7785;
  font-size: 0.75rem;
}
.breadcrumb-item a {
  color: #5b7785;
  text-decoration: none;
}
.breadcrumb-item a:hover {
  color: #303030;
}
.breadcrumb-item.active {
  font-weight: 700;
}
.breadcrumb-item + .breadcrumb-item::before,
.breadcrumb-item:first-child:before {
  content: url('data:image/svg+xml,%3Csvg width="6" height="11" viewBox="0 0 6 11" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M5 1.05469L0.757359 5.29733L5 9.53997" stroke="%238AA3B0" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/%3E%3C/svg%3E%0A');
  font-weight: normal;
  padding: 0.0625rem 1.125rem 0 1.125rem;
}
.breadcrumb-item:first-child:before {
  padding-left: 0;
}
.btn {
  border-radius: 1.875rem;
  border-width: 0;
  font-size: 0.9rem;
  letter-spacing: 0.04em;
  padding: 0.5rem 1.2rem;
  text-decoration: none;
}
.btn:focus,
.btn:active {
  box-shadow: none;
}
.btn[disabled],
.btn.disabled,
.btn[disabled]:focus,
.btn.disabled:focus,
.btn[disabled]:hover,
.btn.disabled:hover,
.btn[disabled]:active,
.btn.disabled:active {
  background-color: #555555;
  border: none;
  box-shadow: none;
  color: #ffffff;
  cursor: not-allowed;
  pointer-events: all;
}
.btn-xs {
  font-size: 0.6rem;
  padding: 0.4rem 0.5rem;
}
.btn-sm {
  font-size: 0.7rem;
  padding: 0.5rem 0.7rem;
}
.btn-lg {
  font-size: 1rem;
  padding: 0.6rem 1.3rem;
}
.btn-primary {
  background-color: #1a3875;
}
.btn-primary:hover {
  background-color: #1a3875;
}
.btn-primary:focus,
.btn-primary:active {
  background-color: #1a3875;
  box-shadow: 0 0 0.25rem #5e9ed6, 0 0 0.5rem #5e9ed6;
  outline: none;
}
.btn-secondary {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 0.125rem #1a3875;
  color: #1a3875;
}
.btn-secondary:hover {
  background-color: #c4d1d7;
  box-shadow: inset 0 0 0 0.125rem #1a3875;
}
.btn-secondary:focus,
.btn-secondary:active {
  background-color: #ffffff;
  box-shadow: 0 0 0.25rem 0.125rem #5e9ed6;
  outline: none;
}
.btn-secondary[disabled],
.btn-secondary.disabled,
.btn-secondary[disabled]:hover,
.btn-secondary.disabled:hover {
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  box-shadow: none;
  color: #b1b1b1;
}
.btn-warning {
  color: #1a3875;
}
.btn-warning:hover,
.btn-warning:focus {
  color: #1a3875;
  outline: none;
}
.btn-success:focus,
.btn-warning:focus,
.btn-danger:focus {
  box-shadow: 0 0 0.25rem 0.125rem #5e9ed6;
  outline: none;
}
.btn-danger {
  background-color: #e91f00;
}
.btn-danger:hover {
  background-color: #b61800;
}
.btn-link {
  border-radius: 0;
  box-shadow: none;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  text-decoration: underline;
}
.btn-link:focus,
.btn-link:active {
  box-shadow: none;
  outline: none;
}
.btn-link:hover {
  text-decoration: underline;
}
.btn-link[disabled],
.btn-link.disabled,
.btn-link[disabled]:hover,
.btn-link.disabled:hover {
  background-color: transparent;
  color: inherit;
  text-decoration: underline;
}
.btn-link.btn-xs {
  font-size: 0.8rem;
}
.btn-link.btn-sm {
  font-size: 0.875rem;
}
.btn-link.btn-lg {
  font-size: 1.25rem;
}
.site-card {
  padding: 1rem;
  background: #ffffff;
  border: 1px solid #f3f6f7;
  border-radius: 0.75rem;
  -webkit-box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.15);
  box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.15);
  height: 100%;
  -webkit-transition: -webkit-box-shadow 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
}
.site-card .panel-heading {
  background-color: #ffffff;
  border-bottom: none;
  color: #e91f00;
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 0.24em;
  line-height: 1.125;
  padding-bottom: 0.75rem;
  text-transform: uppercase;
}
.site-card .panel-body {
  padding-top: 0;
  padding-bottom: 0.5rem;
}
.site-card .panel-title {
  color: #1a3875;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.site-card .panel-subtitle {
  color: #555555;
  font-family: "Open Sans", sans-serif;
  font-size: 0.75rem;
  font-weight: normal;
  margin-bottom: 1rem;
}
.site-card .panel-text {
  color: #555555;
  font-size: 1rem;
}
/*
 * Mobile styles
 */
@media (max-width: 992px) {
  .site-card {
    padding: 0.2rem;
  }
}
.dropdown-menu {
  background-color: #f3f6f7;
  border: 1px solid #1a3875;
}
.dropdown-menu li > a {
  text-decoration: none;
}
.dropdown-menu li > a:hover,
.dropdown-menu li > a:focus {
  background-color: #c4d1d7;
  color: #000000;
  text-decoration: none;
}
.dropdown-menu li > a.active,
.dropdown-menu li > a:active {
  color: #000000;
}
.dropdown-menu li > a.disabled,
.dropdown-menu li > a:disabled {
  color: #000000;
}
