.site-footer {
  background-color: #1a3875;
  bottom: 0;
  color: #e3e3e3;
  font-family: "Open Sans", sans-serif;
  font-size: 0.875rem;
  margin-top: 3.125rem;
  padding: 1.875rem 2rem 1.5rem 2rem;
}
.site-footer a {
  color: #e3e3e3;
  text-decoration: none;
}
.site-footer a:hover {
  color: #ffffff;
  text-decoration: underline;
}
.site-footer--small {
  font-size: 0.75rem;
}
.site-footer--mobile {
  display: none;
}
.site-footer .btn-primary {
  border: 2px solid #ffffff;
  color: #ffffff;
  margin: 1.875rem 0;
}
.site-footer .btn-primary:hover {
  text-decoration: none;
}
.site-footer__main div[class*="col-"]:not(:last-child) {
  padding-right: 2.5rem;
}
.site-footer__main h3,
.site-footer__main h3 a {
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-size: 0.875rem;
  font-weight: bold;
  line-height: 1.5;
  margin-bottom: 1rem;
}
.site-footer__main ul {
  list-style: none;
  padding-left: 0;
}
